.header__list-of-pets {

  &__title {
    padding: 32px 34px;
    text-align: center;
    background-color: $violet100;
    font-size: 24px;
    font-weight: 600;
    line-height: 40px;
    color: $stone900;

    @include lg {
      padding: 48px 0;
      font-size: 40px;
    }
  }
}

.filter {
  padding: 8px 20px;
  display: flex;
  column-gap: 10px;

  @include md {
    column-gap: 15px;
    padding: 8px 40px;
  }

  @include lg {
    column-gap: 32px;
    padding: 8px 40px;
  }

  @include xl {
    column-gap: 32px;
    padding: 8px 136px;
  }
}

