.main__list-of-pets {
  grid-template-columns: 1fr;
  display: grid;
  row-gap: 40px;
  column-gap: 20px;
  padding: 40px 20px;
  background-color: $violet100;

  @include md {
    padding: 56px 40px;
  }

  @include lg {
    row-gap: 56px;
    padding: 56px 40px;
  }

  @include xl {
    padding: 56px 136px;
  }

  .loader__center {
    grid-column-start: 2;
  }

  @include lg {
    grid-template-columns: 1fr 1fr;
  }

  @include xl {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
